/*==========  Desktop First Method  ==========*/

@media only screen and (max-width : 1600px) {

}

@media only screen and (max-width : 1440px) {
  .slider-wrapper{
    .swiper-pagination{
      right: 20px;
    }
  }
}

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {
  .container{
    margin: 0 auto;
  }
  #form{
    margin-left: 15%;
    position: relative;
  }
  .block-form_title{
    position: relative;
    max-width: 390px;
    padding: 20px 0 0 0;
  }
  .program-box{
    float: none;
    margin: 0 auto 22px auto;
    &:nth-child(odd){
      margin: 0 auto 22px auto;
    }
  }
  .block-slider{
    padding: 125px 25px;
  }
  .block-slider_content{
    max-width: 700px;
    margin: 0 auto 40px auto;
    float: none;
  }
  .block-slider_slider{
    float: none;
    margin: 0 auto;
  }
  .elements-list{
    li{
      width: 33.33%;
    }
  }
  .block-price{
    li{
      max-width: 370px;
      width: 31.2%;
      margin: 0 30px 30px 0;
      &:last-child{
        margin: 0 0 30px 0;
      }
    }
  }
  #block-slider{
    .swiper-slide{
      .slider-content{
        q{
          max-width: 525px;
        }
      }
    }
  }
}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {
  #nav{
    max-width: 720px;
    ul{
      .logo{
        margin: 15px 70px;
      }
    }
  }
  .form-wrapper{
    padding: 50px 15px 70px 15px;
  }
  .block-form{
    display: flex;
    flex-direction: column;
  }
  #form{
    margin: 0 auto;
    position: relative;
    order: 2;
  }
  .block-form_title{
    position: relative;
    padding: 0 0 0 0;
    margin: 0 auto 50px auto;
    order: 1;
    max-width: 600px;
    text-align: center;
  }
  .images{
    li{
      width: 50%;
    }
  }
  .block-price{
    li{
      max-width: 310px;
      width: 31.9%;
      margin: 0 15px 30px 0;
      &:last-child{
        margin: 0 0 30px 0;
      }
    }
  }
  #block-slider{
    .swiper-slide{
      .slider-content{
        q{
          &:after{
            right: 20px;
          }
        }
      }
      .box-image{
        margin: 0 88px 0 20px;
      }
    }
  }
  .slider-wrapper{
    .swiper-pagination{
      margin-right: -67px;
      right: 50%;
      top: auto;
      bottom: 5px;
    }
    .swiper-pagination-bullet{
      display: inline-block;
      margin: 0 18px 0 0;
    }
  }
  #block-slider{
    .swiper-slide{
      .slider-content{
        q{
          max-width: 465px;
        }
      }
    }
  }
  .box-contacts{
    padding: 0 20px;
  }
  .box-contact{
    width: 100%;
    margin: 0 0 20px 0;
  }
}

/* Small Devices, Tablets */
@media only screen and (max-width : 768px) {
  .box-logo{
    display: block;
    float: left;
    span{
      display: block;
      font: 24px/28px "ChampagneLimousinesBold", Arial, Helvetica, sans-serif;
      color: #5363db;
      text-transform: uppercase;
      text-align: center;
    }
    a{
      display: inline-block;
      text-decoration: none;
      padding: 20px;
      font: 14px/18px "OpenSansSemiBold", Arial, Helvetica, sans-serif;
      color: #555;
    }
    &:hover{
      a{
        &:after{
          display: none;
        }
      }
    }
  }
  .hamburger{
    float: right;
    padding: 40px 20px 0 0;
    position: relative;
    z-index: 10;
    .line{
      display: block;
    }
  }
  #nav{
    max-width: 100%;
    ul{
      display: none;
      position: absolute;
      right: 0;
      left: 0;
      top: 118px;
      z-index: 2;
      li{
        display: block;
        text-align: center;
        background-color: #fff;
        margin: 0;
        &:hover{
          background-color: #5363db;
          a{
            color: #fff;
            &:after{
              width: 0;
            }
          }
        }
        a{
          width: 100%;
          padding: 25px;
        }
      }
      .logo{
        display: none;
      }
    }
  }
  .block-form_title{
    h1{
      font: 34px/64px "Open Sans", Arial, Helvetica, sans-serif;
      margin: 0 0 10px 0;
    }
    h2{
      font: 30px/36px "Freestyle Script", Arial, Helvetica, sans-serif;
    }
  }
  #form{
    margin: 0 auto;
    order: 2;
  }
  .program-box{
    width: 100%;
    max-width: 269px;
    padding: 0 0 30px 0;
    img{
      border: 0 solid;
      border-radius: 0 8px 0 0;
    }
  }
  .elements-list{
    li{
      width: 50%;
    }
  }
  .block-price{
    li{
      width: 100%;
      max-width: 370px;
      margin: 0 auto 30px;
      display: block;
      &:last-child{
        margin: 0 auto 30px;
      }
    }
  }
  #block-slider{
    height: 450px;
    .swiper-slide{
      .box-image{
        float: none;
        margin: 0 auto 30px;
      }
      .slider-content{
        float: none;
        margin: 0 auto;
        text-align: center;
        padding: 0 20px;
        q{
          &:before{
            left: -20px;
            top: 0;
          }
        }
      }
    }
  }
}

/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px) {
  .form-wrapper{
    padding: 50px 10px 70px 10px;
  }
  .elements-list{
    li{
      width: 100%;
    }
  }
  #block-slider{
    height: 520px;
  }
  .block-info{
    padding: 60px 15px 50px;
  }
  .box-info{
    width: 100%;
    float: none;
    margin: 0 0 70px 0;
  }
  .box-contacts{
    width: 100%;
    float: none;
    padding: 0;
  }
}