.hamburger .line {
  width: 50px;
  height: 5px;
  background-color: #8888E7;
  display: none;
  margin: 8px auto;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.hamburger:hover {
  cursor: pointer;
}

/* ONE */

#hamburger-1.is-active .line:nth-child(2) {
  opacity: 0;
}

#hamburger-1.is-active .line:nth-child(1) {
  -webkit-transform: translateY(13px) rotate(45deg);
  -ms-transform: translateY(13px) rotate(45deg);
  -o-transform: translateY(13px) rotate(45deg);
  transform: translateY(13px) rotate(45deg);
}

#hamburger-1.is-active .line:nth-child(3) {
  -webkit-transform: translateY(-13px) rotate(-45deg);
  -ms-transform: translateY(-13px) rotate(-45deg);
  -o-transform: translateY(-13px) rotate(-45deg);
  transform: translateY(-13px) rotate(-45deg);
}

#nav {
  width: 100%;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  ul {
    padding: 0;
    margin: 0;
    list-style: none;
    line-height: 0;
    font-size: 0;
    letter-spacing: 0;
    li {
      display: inline-block;
      margin: 0 5px;
      vertical-align: middle;
      position: relative;
      transition: all .4s;
      a {
        display: inline-block;
        text-decoration: none;
        padding: 10px;
        font: 14px/18px "OpenSansSemiBold", Arial, Helvetica, sans-serif;
        color: #555;
      }
      &:hover {
        a {
          font: 14px/18px "OpenSansSemiBold", Arial, Helvetica, sans-serif;
          color: #5363db;
          &:after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 50%;
            margin-left: -15px;
            width: 30px;
            height: 1px;
            background-color: #5363db;
          }
        }
      }
      .active{
        font: 14px/18px "OpenSansSemiBold", Arial, Helvetica, sans-serif;
        color: #5363db;
        &:after {
          content: '';
          position: absolute;
          bottom: 0;
          left: 50%;
          margin-left: -15px;
          width: 30px;
          height: 1px;
          background-color: #5363db;
        }
      }
    }
    .logo {
      margin: 12px 112px;
      span {
        display: block;
        font: 24px/28px "ChampagneLimousinesBold", Arial, Helvetica, sans-serif;
        color: #5363db;
        text-transform: uppercase;
        text-align: center;
      }
      a {
        padding: 0;
      }
      &:hover {
        a {
          &:after {
            display: none;
          }
        }
      }
    }
  }
}

.form-wrapper {
