#form {
  float: left;
  padding: 40px 40px 35px 30px;
  background-color: #fff;
  display: inline-block;
  border: 0 solid;
  border-radius: 20px;
  box-shadow: 5.7px 5.7px 10px rgba(0, 0, 0, 0.15);
  margin-left: 23%;
  max-width: 300px !important;
  h2 {
    font: 25.64px/28px "OpenSansSemiBold", Arial, Helvetica, sans-serif;
    color: #5363db;
    margin: 0 0 30px 0;
  }
  input[type="text"],
  select {
    width: 230px !important;
    border: 0 solid;
    border-radius: 8px;
    text-indent: 10px;
    font: 14px/18px "Open Sans", Arial, Helvetica, sans-serif;
    color: #888;
    display: block;
    margin: 0 0 17px 0;
    padding: 15px 10px 17px 0;
    background-color: #f2f2f2;
  }
  select {
    margin: 0 0 30px 0;
    text-indent: 3px;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
  }
  input[type="text"]::placeholder {
    font: 14px/18px "Open Sans", Arial, Helvetica, sans-serif;
    color: #888;
  }
  input[type="submit"] {
    font: 16px/20px "Open Sans", Arial, Helvetica, sans-serif;
    font-weight: 700;
    border: 0 solid;
    background-color: #5b6ceb;
    border-radius: 8px;
