.slider-wrapper{
  position: relative;
  .swiper-pagination{
    right: -64px;
    top: 50%;
    margin-top: -67px;
  }
  .swiper-pagination-bullet{
    width: 20px;
    height: 20px;
    background-color: transparent;
    border: 2px #d6d6d6 solid;
    opacity: 1;
    padding: 3px;
    display: block;
    margin: 0 0 18px 0;
    &:last-child{
      margin: 0;
    }
  }
  .swiper-pagination-bullet-active{
    position: relative;
    width: 20px;
    height: 20px;
    border: 2px #5b6ceb solid;
    padding: 3px;
    &:before{
      content: '';
      position: absolute;
      top: 3px;
      left: 3px;
      width: 10px;
      height: 10px;
      background-color: #5b6ceb;
      border: 0 solid;
      border-radius: 50%;
    }
  }
}

#block-slider{
  box-shadow: 6.4px 6.4px 50px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  border: 0 solid;
  height: 250px;
  position: relative;
  background-color: #fff;
  .box-slider{
    padding: 40px 0 72px 0;
    height: 250px;
  }
  .swiper-slide{
    max-width: 1170px;
    width: 100%;
    .box-image{
      border: 2px #d7d7d7 solid;
      border-radius: 50%;
      width: 148px;
      height: 148px;
      margin: 0 88px 0 62px;
      float: left;
      img{
        margin: 11px auto 0;
        width: 125px;
        height: 125px;
        display: block;
      }
    }
    .slider-content{
      font: 14px/22px "Open Sans", Arial, Helvetica, sans-serif;
      font-style: italic;
      color: #555;
      max-width: 645px;
      float: left;
      margin: 10px 0 0 0;
      blockquote{
        max-width: 645px;
        margin: 0;
      }
      q{
        max-width: 645px;
        width: 100%;
        position: relative;
        display: inline-block;
        margin: 0 0 32px 0;
        quotes: '\201c' '\201d';
        &:before{
          content: open-quote;
          font-size: 60px;
          position: absolute;
          left: -50px;
          color: #5b6ceb;
          top: 12px;
        }
        &:after{
          content: close-quote;
          font-size: 60px;
          position: absolute;
          right: -60px;
          bottom: -30px;
          color: #5b6ceb;
        }
      }
      cite{
        display: block;
        position: relative;
        &:before{
          content: '';
          position: absolute;
          left: -35px;
          top: -35px;
          width: 2px;
          height: 75px;
          background-color: #d7d7d7;
        }
        span{
          display: block;
          font: normal 14px/22px "Open Sans", Arial, Helvetica, sans-serif;
          color: #777;
        }
        b{
          display: block;
          font: normal 16px/22px "Open Sans", Arial, Helvetica, sans-serif;
          color: #5b6ceb;
          font-weight: 700;
        }
      }
    }
  }
}

